<!--
 * @Author Demon3443002624@qq.com
 * @Version 2022-5-18 14:43:51
 * @Description 报警历史列表界面
 * @Version 2022-5-18 14:44:14
 * @Description 本页面不需要分页
-->
<template>
  <div>
    <BMainTopComponent />
    <el-container>
      <el-main>
        <div id="crew-record-alert-history">
          <!-- <toggle-crew ></toggle-crew> -->
          <el-card shadow="never">
            <div class="block">
              <span>筛选：</span>
              <el-date-picker
                v-model="value_date"
                type="daterange"
                range-separator="至"
                start-placeholder="开始时间"
                end-placeholder="结束时间"
                format="yyyy-MM-dd"
                :popper-append-to-body="false"
                placement="bottom-start"
              >
              </el-date-picker>
            </div>
            <el-row type="flex" align="middle">
              &nbsp;&nbsp;&nbsp;&nbsp;类型：
              <el-select v-model="value_type" placeholder="请选择">
                <el-option
                  v-for="item in typeOpt"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-row>
            <el-button type="primary" @click="_query">搜索</el-button>
          </el-card>
          <el-card shadow="never">
            <table-list
              :options="options"
              :columns="columns"
              :operates="operates(this)"
              :dataSource="dataSource"
              :dataTotal="dataTotal"
              :pagination="paginationCfg"
              @handleChangePage="handleChangePage"
              @handleChangeSize="handleChangeSize"
            />
          </el-card>
          <el-dialog title="查看记录曲线" :visible.sync="dialogVisible" width="50%">
            <el-row type="flex" justify="center">
              机组名称：{{ selectCrew.DisplayName }} 报警时间：{{ currentAlertTime }} 报警名称：{{
                currentAlertContent
              }}
            </el-row>
            <div class="record-curve">
              <record-curve :curveData="curveData" :dataType="dataType" />
            </div>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">关闭</el-button>
              <!-- <el-button type="primary" @click="dialogVisible = false"
          >确 定</el-button
        > -->
            </span>
          </el-dialog>
        </div>
      </el-main>
      <toggle-crew></toggle-crew>
    </el-container>
  </div>
</template>

<script>
import dayjs from 'dayjs'
import { mapGetters } from 'vuex'

// import _data from '../data'
import { formatTime } from '@/utils/common'
import ToggleCrew from '@/components/toggleCrew/list'
import RecordCurve from '@/components/RecordCurve'

// 表格头设置
const columns = [
  {
    label: '内容',
    prop: 'content',
    props: {
      // sortable: 'sortable'
    }
  },
  {
    label: '类型',
    prop: 'type'
  },
  {
    label: '开始时间',
    prop: 'startTime'
  },
  {
    label: '结束时间',
    prop: 'endTime'
  }
]
// 表格操作按钮
const operates = (that) => [
  {
    label: '查看记录曲线',
    method: (row) => {
      that.handleNewJump(row)
    }
  }
]
export default {
  name: `CrewRecordAlertHistory`,
  components: {
    ToggleCrew,
    RecordCurve
  },
  data() {
    return {
      columns,
      operates,
      dataSource: [],
      dataTotal: 0,
      options: {
        index: true,
        labelIndex: '序号'
      },
      paginationCfg: {
        currentPage: 1,
        pageSize: 9999,
        // layout: 'total, sizes, prev, pager, next, jumper',
        pageSizes: [9999]
      },
      data: [],
      value_date: [new Date(new Date().setDate(new Date().getDate() - 7)), new Date()],
      value_type: '-1',
      queryOpt: {},
      typeOpt: [
        {
          value: '-1',
          label: '全部'
        },
        {
          value: 0,
          label: '警告'
        },
        {
          value: 1,
          label: '停机'
        }
      ],
      dialogVisible: false,
      dataType: [
        'r_010',
        'r_011',
        'r_012',
        'r_013',
        'r_014',
        'r_015',
        'r_019',
        'r_047',
        'r_048',
        'r_050',
        'r_051',
        'r_052',
        'r_055'
      ],
      currentAlertContent: '',
      currentAlertTime: '',
      curveData: null
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  watch: {
    selectCrew: {
      handler: function (nv) {
        this._query()
      }
    }
  },
  created() {
    this.queryOpt = {
      value_date: this.value_date,
      value_type: this.value_type
    }
  },
  mounted() {
    this._query()
  },
  methods: {
    // 查询数据
    queryTableData() {
      const q = {
          id: this.selectCrew.ID
        },
        p = {
          // TimeStart: dayjs(this.value_date[0]).format('YYYY-MM-DD'),
          // TimeEnd: dayjs(this.value_date[1]).format('YYYY-MM-DD'),
          // Mode: this.value_type
          TimeStart: dayjs(this.queryOpt.value_date[0]).format('YYYY-MM-DD'),
          TimeEnd: dayjs(this.queryOpt.value_date[1]).format('YYYY-MM-DD'),
          Mode: this.queryOpt.value_type
        }
      this.$api.equipment
        .findGeneratorSetHistoryAlarm(p, q)
        .then((value) => {
          // value = _data.alert
          const dataObj = !!value && !!value.Data && value.Data
          if (dataObj) {
            const arr = []
            const dataList = dataObj.Alarms
            !dataList ||
              dataList.length === 0 ||
              dataList.forEach((e, i) => {
                const obj = {
                  key: i,
                  content: e.Describe,
                  type: e.TypeName,
                  startTime: formatTime(e.StartTime).format('YYYY-MM-DD HH:mm:ss'),
                  endTime: e.EndTime ? formatTime(e.EndTime).format('YYYY-MM-DD HH:mm:ss') : '-'
                }
                arr.push(obj)
              })
            this.dataTotal = dataList.length || 0
            if (this.dataTotal > this.paginationCfg.pageSize) {
              this.$message.error(
                `数据量超出限制${this.paginationCfg.pageSize}，请重新选择筛选条件！`
              )
            } else {
              this.$set(this, 'dataSource', arr)
            }
          }
        })
        .catch(() => {})
    },
    handleNewJump(row) {
      this.getCurveData(row.startTime)
      this.dialogVisible = true
      this.currentAlertTime = dayjs(row.startTime).format('MM-DD HH:mm')
      this.currentAlertContent = row.content
    },
    // 接口 获取曲线数据
    getCurveData(sT) {
      // 开始时间前后15分钟
      const p = {
          TimeStart: dayjs(sT).add(-15, 'm'),
          TimeEnd: dayjs(sT).add(15, 'm'),
          Items: Array.from(this.dataType).join(','),
          Type: '0'
        },
        q = {
          id: this.selectCrew.ID
        }
      this.$api.equipment
        .findGeneratorSetHistoryDataV3(p, q)
        .then((value) => {
          // value = _data.curveHIstory
          !!value && !!value.Data && (this.curveData = value.Data)
        })
        .catch((result) => {
          console.error('获取曲线数据失败', result)
        })
    },
    // 分页 当前页改变
    handleChangePage(val) {
      this.paginationCfg.currentPage = val
      this.queryTableData()
    },
    // 分页 每页条数改变
    handleChangeSize(val) {
      this.paginationCfg.currentPage = 1
      this.paginationCfg.pageSize = val
      this.queryTableData()
    },
    // 搜索
    _query() {
      this.paginationCfg.currentPage = 1
      this.queryOpt = {
        value_date: this.value_date,
        value_type: this.value_type
      }
      this.queryTableData()
    }
  }
}
</script>

<style lang="less" scoped>
#crew-record-alert-history {
  /deep/.el-card {
    overflow: initial;
    margin-bottom: 15px;
    .el-card__body {
      display: flex;
      .el-button {
        margin: 0 0 0 10px;
      }
      .el-input__inner {
        height: 32px;
      }
    }
  }
  /deep/ .toggle-crew {
    margin-bottom: 0;
  }
  /deep/ .el-dialog__body {
    padding: 5px;
  }
  /deep/ .el-pagination {
    display: none;
  }
  .record-curve {
    height: 500px;
  }
}
</style>



