<template>
  <div
    :class="'record-curve' + className"
    :style="{
      height: '100%',
      width: '100%'
    }"
  ></div>
</template>

<script>
import dayjs from 'dayjs'

import __data from './data'
import { formatTime } from '@/utils/common'

export default {
  name: `RecordCurve`,
  data() {
    return {
      chart: null
    }
  },
  props: {
    dataType: {
      type: Array,
      default: (_) => {
        return []
      }
    },
    className: '',
    curveData: {
      type: Object,
      default: (_) => {
        return {}
      }
    }
  },
  watch: {
    dataType(nv, ov) {
      this.renderChart()
    },
    curveData(nv){
      this.renderChart()
    }
  },
  methods: {
    renderChart() {
      const countTime0 = this.$moment()
      this.chart && this.$echarts.dispose(this.chart)
      this.chart = this.$echarts.init(
        document.querySelector(`.record-curve${this.className}`),
        `dark`
      )
      const myChart = this.chart
      const moment = this.$moment
      const _data = this.curveData
      if (_data && Array.from(_data.Datas).length != 0) {
        myChart.showLoading({
          text: '数据加载中，请稍候...',
          textColor: '#eee',
          maskColor: 'rgba(16, 12, 42)',
          zlevel: 5,
          fontSize: 20,
          showSpinner: false
        })
        setTimeout(() => {
          const data0 = []
          _data.Datas.forEach((item) => {
            if (this.dataType.includes(item.No)) data0.push(item)
          })
          const dataD = {}
          dataD.Datas = data0
          dataD.EndTime = _data.EndTime
          dataD.StartTime = _data.StartTime
          dataD.GeneratorSetID = _data.GeneratorSetID
          dataD.Nos = _data.Nos
          var option = this.getDefaultOption()
          var series = []
          var date = []
          var startDate = formatTime(dataD.StartTime)

          var endDate = formatTime(dataD.EndTime)
          var startDataZoom = 0
          var endDataZoom = 10
          for (; startDate <= endDate; startDate = startDate.add(1, 's')) {
            date.push(startDate.format('yyyy-MM-DD HH:mm:ss'))
          }
          option.xAxis.data = date
          const datas = dataD
          for (var index in datas.Datas) {
            var data = datas.Datas[index]
            var items = data.Data
            option.legend.data.push(data.Describe)
            series[index] = {
              name: data.Describe,
              type: 'line',
              stack: '',
              data: [],
              showSymbol: true,
              showAllSymbol: false,
              symbol: 'emptyCircle', // 设定为实心点
              symbolSize: 5, // 设定实心点的大小
              // hoverAnimation: true,
              // hoverAnimation is deprecated, use emphasis.scale instead.
              emphasis: {
                scale: true
              },
              // animation: true,
              // 解决大数据加载卡顿问题 1
              animation: false,
              // 解决大数据加载卡顿问题（ Echats 官方）-->有问题
              // sampling: 'average',
              tooltip: {
                formatter: function (params) {
                  return (
                    params.name +
                    '<br/>' +
                    params.seriesName +
                    '：' +
                    params.value +
                    ' ' +
                    items[0].Unit
                  )
                }
              }
            }
            var curValue = '-'
            var itemIndex = 0
            var itemTime = moment(items[itemIndex].Time)
            var curValueLast = '-'
            var curFirst = 0
            // 生成数据
            // for (var i = 0; i < date.length; i++) {
            //   const a = moment(date[i])
            //   const b = moment(date[i])
            // }
            // date.forEach((item) => {
            //   // const a = moment(item)
            //   // const b = moment(item)
            //   // const a = Math.random()
            //   // const b = Math.random()
            //   const a = dayjs(item)
            //   const b = dayjs(item)
            // })
            for (var i = 0; i < date.length; i++) {
              var curDate = dayjs(date[i])
              curValue = items[itemIndex].Value
              itemTime = dayjs(items[itemIndex].TimeString)

              if (curDate <= itemTime) {
                if (curFirst === 0 && itemTime - curDate >= 1000) {
                  curValue = '-'
                } else if (itemTime - curDate < 1000) {
                  curValue = items[itemIndex].Value
                  curValueLast = curValue
                  curFirst = 1
                  if (items.length > itemIndex + 1) itemIndex++
                } else {
                  curValue = curValueLast
                  if (itemTime - curDate >= 300000) {
                    curFirst = 0
                    curValue = '-'
                  }
                }
              } else if (curFirst === 1 && curDate > itemTime) {
                // curValue = curValueLast;//最后一条数据补点
                curValue = '-' // 最后一条数据不补点
                if (items.length > itemIndex + 1) itemIndex++
              }

              series[index].data.push(curValue)
            }
          }
          option.series = series
          option.dataZoom = [
            {
              type: 'inside',
              start: 0,
              backgroundColor: 'rgba(47,69,84,0)',
              // 解决大数据加载卡顿问题 2
              realtime: false,
              throttle: 0
            },
            {
              start: startDataZoom,
              end: endDataZoom,
              // 解决大数据加载卡顿问题 2
              realtime: false,
              throttle: 0,
              dataBackground: {
                // 数据阴影的样式。
                lineStyle: {
                  opacity: 0
                },
                areaStyle: {
                  opacity: 0
                }
              },
              // 隐藏数据阴影
              selectedDataBackground: {
                lineStyle: {
                  opacity: 0
                },
                areaStyle: {
                  opacity: 0
                }
              }
            }
          ]
          myChart.hideLoading()
          myChart.setOption(option)
          const interval = this.$moment().diff(countTime0)
          console.info(`info:ECharts finished in ${interval}ms`)
        }, 100)
      }
    },
    getDefaultOption() {
      return {
        tooltip: {
          trigger: 'axis',
          position: function (pt) {
            return [pt[0], '10%']
          },
          textStyle: {
            color: '#fff'
          },
          backgroundColor: '#6f6f6f89',
          confine: true
        },
        toolbox: {
          show: false,
          feature: {
            dataView: { show: true, readOnly: false },
            saveAsImage: { show: true }
          }
        },
        title: { left: 'left' },
        legend: {
          x: '10%',
          y: '10px',
          type: 'scroll',
          orient: 'horizontal', // ''
          data: [],
          width: '80%'
        },
        xAxis: {
          // name: '时间',
          name: '',
          type: 'category',
          boundaryGap: false,
          data: [],
          axisLine: {
            lineStyle: {
              color: '#eedd78'
            }
          },
          axisLabel: {
            color: '#eedd78'
          },
          axisPointer: {
            lineStyle: {
              color: '#eedd78',
              type: 'solid'
            }
          }
        },
        yAxis: {
          name: '',
          type: 'value',
          boundaryGap: ['20%', '20%'],
          min: 0,
          axisLine: {
            show: true,
            lineStyle: {
              color: '#eedd78'
            }
          },
          splitLine: {
            lineStyle: {
              type: 'dashed',
              color: '#aaaaaa'
            }
          }
        }
      }
    }
  },
  mounted() {
    this.renderChart()
  }
}
</script>

<style lang="less" scoped>
.record-curve {
  width: 100%;
  height: 100%;
}
</style>



